body, html, .App {
    margin: 0;
    padding: 0;
    background-color: #0a0a0a;
    color: #fff;
    font-family: 'Segoe UI', sans-serif;
    text-align: center;
}

.section {
    padding: 4rem 1rem;
    max-width: 1200px;
    margin: auto;
}

/* Header */
.header {
    padding: 2rem 1rem;
    border-bottom: 1px solid #333;
}

.profile-pic {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 0.5rem;
}

.header h1 {
    margin: 0.5rem 0;
    font-size: 2rem;
}

.header nav {
    margin-top: 1rem;
}

    .header nav a {
        color: #9c6bff;
        margin: 0 1rem;
        text-decoration: none;
        font-weight: bold;
    }

        .header nav a:hover {
            text-decoration: underline;
        }

/* Projects */
.project-section {
    background-color: #111;
    border-top: 1px solid #333;
    border-bottom: 1px solid #333;
}

.project-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
    margin-top: 2rem;
}

.project-card {
    background-color: #1a1a1a;
    border: 1px solid #444;
    border-radius: 8px;
    padding: 1.5rem;
}

.project-image {
    width: 100%;
    border-radius: 6px;
    margin-bottom: 1rem;
}

.project-links a {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    margin: 0 0.5rem;
    color: #9c6bff;
    font-weight: bold;
    text-decoration: none;
}

    .project-links a:hover {
        text-decoration: underline;
    }

/* Contact */
.contact-links {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
    align-items: center;
    margin-top: 2rem;
}

    .contact-links a {
        color: #9c6bff;
        font-weight: bold;
        text-decoration: none;
        display: flex;
        align-items: center;
        gap: 6px;
    }

        .contact-links a:hover {
            text-decoration: underline;
        }

/* Footer */
.footer {
    padding: 1rem;
    background-color: #111;
    border-top: 1px solid #444;
    font-size: 0.9rem;
}
